.x-maincompose {
    cursor: pointer;
    border: none;
    outline: none;
    background: none;
    font-size: 1.25rem;
    font-weight: bold;
    margin-right: 10px;
    margin-left: 2.5%;
}

.x {
    cursor: pointer;
    border: none;
    outline: none;
    background: none;
    font-size: 1.25rem;
    font-weight: bold;
    position: absolute;
    right: 20px;
    top: 20px;
}

.modalc-wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.modalc-backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background-color: rgba(0,0,0,0.3);
}

.modalc-box {
    position: relative;
    margin-left: "15%";
    margin-top: "9%";
    width: "48%";
    height: "74%";
    overflow-y: auto;
    z-index: 101;
}

.modalc-body {
    padding: 10px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: Abel;
}

.trash {
    height: 40px;
    width: 160px;
    background-color: #274D5A;
    border: 2px solid #274D5A;
    color: white;
    background-repeat: no-repeat;
    outline: none;
    cursor: pointer;
    text-align: center;
    margin: auto;
    display: flex;
    flex-direction: row;
    font-family: Abel;
    justify-content: right;
    align-items: right;
}

.trash:hover {
    background-color: #407c91;
    border: 2px solid #407c91;
    color: white;
  }

.savebutton {
    background-color: #ffffff;
    border-radius: 4px;
    border: 1px solid;
    color: #000000;
    padding: 10px 24px;
    display: inline-block;
    margin-right: 10px;
    margin-left: 2.5%;
    width: 150px;
    font-family: Abel;
    font-size: 16px;
}

.savebutton:hover {
    background-color: #c7c7c7;
    border: 1px solid #c7c7c7;
    color: black;
    border-radius: 4px;
    padding: 10px 24px;
  }

