/* Split the screen in half */
.split {
    height: 675px;
    width: 50%;
    z-index: 1;
    position: absolute;
    overflow-x: hidden;
    padding-top: 300px;
  }
  
  /* Control the left side */
  .left {
    left: 0;
  }
  
  /* Control the right side */
  .right {
    right: 0;
  }

  /* If you want the content centered horizontally and vertically */
  .centered {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }

